/* When a component is used on every page, it belongs here */

:root {
  --white: #ffffff;
  --gray1: #f4f4f8;
  --gray2: #e6e6ea;
  --gray3: #b1b1b4;
  --gray4: #979797;
  --gray5: #737375;
  --gray6: #4f4f50;
  --gray7: #39393a;
  --gray8: #232324;
  --gray9: #141414;
  --black: #000000;

  --red1: red;
  --red2: rgb(179, 0, 0);
  --red3: rgb(129, 0, 0);
  --red4: rgb(58, 0, 0);

  --orange1: #ff6c00;
  --orange2: #b1461f;
  --orange3: #bb7900;
  --orange4: #ed9a00;

  --green1: rgb(0, 255, 0);
  --green2: rgb(0, 180, 0);
  --green3: green;
  --green4: rgb(0, 59, 0);

  --highlight-blue-1: rgb(205, 205, 255);
  --highlight-blue-2: rgb(182, 182, 255);
  --highlight-blue-3: rgb(58, 58, 185);

  --blue1: rgb(0, 162, 255);
  --blue2: blue;
  --blue3: rgb(0, 0, 182);
  --blue4: rgb(0, 0, 133);
  --blue5: rgb(0, 0, 66);

  --push-height: 500px;
  --short-push-height: 175px;
}

.light-theme {
  --bg: var(--white);
  --base: var(--white);
  --highlighted-0: #eeeeee;
  --highlighted-1: var(--highlight-blue-1);
  --highlighted-2: var(--highlight-blue-2);

  --text: var(--black);
  --text-inverted: var(--white);
  --text-link: var(--link2);
  --text-player-number: blue;
  --text-lfm-number: var(--orange2);
  --text-disabled: var(--gray3);
  --text-faded: var(--gray6);
  --text-xfaded: var(--gray3);
  --text-slight-green: var(--green3);

  --red-text: var(--red1);
  --green-text: var(--green2);
  --blue-text: #0026d1;

  --table-odd-rows: rgb(58, 58, 185, 0.1);
  --table-highlight: rgb(58, 58, 185, 0.2);

  --server-nav-bar: var(--blue5);
  --server-nav-bar-hover: var(--blue2);
  --server-nav-bar-active: var(--blue3);
  --server-nav-bar-active-text: var(--white);
  --server-nav-bar-text: var(--black);

  --nav-bar: var(--gray7);
  --nav-bar-hover: var(--gray6);
  --nav-bar-active: var(--blue3);

  --filter-bar: var(--highlight-blue-1);
  --filter-bar-hover: var(--highlight-blue-2);
  --filter-bar-active: var(--blue3);

  --slider-handle: var(--orange3);
  --slider-track: var(--orange4);
  --slider-rail: var(--gray4);

  --blue-text: #0026d1;

  --primary-button-background: var(--highlight-blue-3);
  --secondary-button-border: var(--highlight-blue-3);

  --faded-blue: #d9dbe6;

  --beta: #f18ddc;
  --soon: #e48e59;
  --new: #aaffff;

  --donate: #e99b0c;
}

.dark-theme {
  --bg: var(--gray8);
  --base: var(--gray8);
  --highlighted-0: #2d2d2d;
  --highlighted-1: var(--gray7);
  --highlighted-2: var(--gray6);

  --text: var(--white);
  --text-inverted: var(--black);
  --text-link: var(--link1);
  --text-player-number: var(--blue1);
  --text-lfm-number: var(--orange1);
  --text-disabled: var(--gray4);
  --text-faded: var(--gray3);
  --text-xfaded: var(--gray5);
  --text-slight-green: var(--green4);

  --red-text: var(--red1);
  --green-text: var(--green1);
  --blue-text: #00aaff;

  --table-odd-rows: var(--gray7);
  --table-highlight: var(--gray6);

  --server-nav-bar: var(--blue4);
  --server-nav-bar-hover: var(--blue3);
  --server-nav-bar-active: var(--blue6);
  --server-nav-bar-active-text: var(--black);
  --server-nav-bar-text: var(--white);

  --nav-bar: var(--gray8);
  --nav-bar-hover: var(--gray7);
  --nav-bar-active: var(--purple2);

  --filter-bar: var(--gray7);
  --filter-bar-hover: var(--gray6);
  --filter-bar-active: var(--blue3);

  --slider-handle: var(--orange3);
  --slider-track: var(--orange4);
  --slider-rail: var(--gray4);

  --primary-button-background: var(--text);
  --secondary-button-border: var(--text);

  --faded-blue: rgb(42, 45, 46);

  --beta: #b64fa0;
  --soon: #aa5e32;
  --new: #339999;

  --donate: #e99b0c;
}

html {
  font-size: 15px;
  scroll-behavior: smooth;
  scroll-padding-top: 70px;
  overflow-y: scroll !important;
}

@-moz-document url-prefix() {
  html {
    scroll-behavior: auto;
  }
}

body {
  font-family: "Times New Roman", Times, serif;
  background-color: var(--bg);
  overflow-x: hidden;
}

.footer {
  position: absolute;
  /* z-index: 4; */
  width: 100%;
  color: white;
  background-color: #333;
  margin-bottom: 0px;
  padding-top: 4px;
  padding-bottom: 6px;
}

.footer-text {
  font-style: italic;
  margin-bottom: 0px;
  line-height: 20px;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

#banner-image {
  position: fixed;
  /* transform: translateY(50%); */
  /* z-index: 1; */
  /* Typical gradient:
        rgba(97, 97, 97, 0.466),
        rgba(15, 15, 15, 0.774)
    */
  background-image: linear-gradient(
      to bottom,
      rgba(97, 97, 97, 0.466),
      rgba(15, 15, 15, 0.774)
    ),
    url("./assets/directory/banner.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: var(--push-height);
  width: 100%;
  background-color: var(--base);
}

#content-push-top {
  height: var(--push-height);
  visibility: hidden;
  /* z-index: 1; */
}

#banner-text-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  /* width: 100%; */
  /* top: 140px; */
  /* height: 500px; */
  /* z-index: 1; */
  padding: 0px 20px;
  user-select: none;
  /* transition: height 200ms ease; */
}

#main-title {
  font-weight: bold;
  font-size: 3rem;
  color: white;
}

#main-subtitle {
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  color: white;
}

.action-button-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  color: var(--text);
}

.action-button-container.vote-buttons {
  line-height: 24px;
  flex-direction: row;
  position: absolute;
  width: 100%;
  transition: opacity 500ms ease;
}

.theme-icon {
  transform: rotate(0deg);
  transition: transform 200ms;
}

.theme-container:hover > .theme-icon {
  transform: rotate(45deg);
}

.options-container {
  position: fixed;
  color: white;
  top: 10px;
  right: 15px;
  height: 35px;
  position: absolute;
  display: flex;
  flex-direction: row;
  z-index: 5;
  gap: 10px;
}

.theme-container {
  font-size: 1.1rem;
  opacity: 0.7;
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}

.theme-container:hover {
  opacity: 1;
}

.card {
  width: 100%;
  max-width: 1920px;
  background-color: var(--base);
  /* box-shadow: 0px 0px 12px 0px var(--card-border);
    border-color: var(--card-border); */
  color: var(--text);
  border-radius: 3px;
  padding: 10px 20px 10px 20px;
  /* margin-left: 10px;
    margin-right: 10px; */
  font-family: "Times New Roman", Times, serif;
  /* margin-bottom: 15px; */
  overflow: hidden;
  transition: background-color 500ms, color 1000ms;
  border: none;
}

.card-title {
  color: var(--text);
  margin: 0px 0px 10px 0px;
}

.card-subtitle {
  color: var(--text-faded);
  font-size: larger;
  margin: 0px;
}

@media (max-width: 600px) {
  .action-button-container {
    flex-direction: column;
    --push-height: 300px;
  }

  #main-title {
    font-size: xx-large;
  }

  #main-subtitle {
    font-size: x-large;
  }
}

.nav-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
  justify-content: left;
  color: whitesmoke !important;
  padding: 10px 10px 10px 10px;
  line-height: 100%;
  height: 58px;
  transition-property: background-color;
  transition-duration: 300ms;
}

.nav-bar {
  /* z-index: 4; */
  display: flex;
  flex-direction: row;
  transition: background-color 500ms;
  overflow: hidden;
}

.nav-title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.nav-pull {
  display: none;
  transform: translateX(140%);
  right: 40px;
  top: 68px;
  background-color: var(--nav-bar);
}

.nav-push {
  display: none;
  height: 55px;
}

.nav-item a {
  text-decoration: none;
  color: inherit;
}

.nav-item a a:hover a:focus a:active {
  text-decoration: none;
  color: inherit;
}

.nav-icon-large {
  width: 32px !important;
  height: 32px !important;
}

.nav-icon-small {
  width: 24px !important;
  height: 24px !important;
}

@media (max-width: 768px) {
  html {
    font-size: 12px;
  }
}

.show-on-mobile {
  display: none !important;
}

.top-content-padding {
  height: 3rem;
}

.top-content-padding-small {
  height: 1rem;
}

.data-classification-content {
  cursor: pointer;
  display: flex;
  align-items: center;
  text-decoration: none !important;
  color: var(--text) !important;
}

.data-classification-content:hover .data-classification-icon {
  filter: grayscale(0%) !important;
}

.link-icon.pin {
  transform: rotate(30deg);
  transition: transform 300ms;
  opacity: 0.2;
}

.link-icon.pin:hover {
  transform: rotate(0deg);
}

.link-icon-pinned {
  opacity: 1;
}

.population-number {
  color: var(--text-player-number);
}

.blue-link {
  color: var(--text-player-number);
  text-decoration: underline;
  cursor: pointer;
  user-select: none;
}

.lfm-number {
  color: var(--text-lfm-number);
}

.red-text {
  color: var(--red-text);
}

.green-text {
  color: var(--green-text);
}

.box-shadow {
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.05);
}

/* .nav-item .active {
    background-color: var(--nav-bar-active);
} */

/* .nav-item:hover .nav-icon {
    width: 30px;
    height: 30px;
} */

.nav-item:not(.active):hover > .nav-title {
  text-shadow: 0px 0px 7px white;
}

.nav-item:hover .nav-title {
  text-decoration: underline;
}

/* .nav-item:hover {
    background-color: var(--nav-bar-hover);
} */

.nav-item.selected {
  background-color: #993333;
}

.nav-icon {
  margin-right: 5px;
  width: 24px;
  height: 24px;
  transition-property: width;
  transition-duration: 200ms;
}

.nav-icon {
  width: 30px;
  height: 30px;
}

.vote-icon {
  opacity: 0.4;
  cursor: pointer;
  width: 40px;
  height: 40px;
}

.vote-icon:hover {
  opacity: 1;
}

.vote-icon.inactive {
  opacity: 0.2;
}

.vote-icon.active {
  opacity: 1;
}

.poll {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.poll-option {
  display: flex;
  flex-direction: column;
}

.poll-options {
  display: flex;
  flex-direction: row;
}

.poll-title {
  color: var(--text);
  font-size: 1.5rem;
}

.nav-item.active {
  text-decoration: underline;
  filter: drop-shadow(0px 0px 7px rgba(255, 255, 255, 0.835));
}

.nav-divider {
  height: 1px;
  background-color: var(--gray5);
}

.nav-dropdown {
  border-radius: 7px;
  position: absolute;
  float: left;
  display: none;
  transform: translateY(55%);
  background-color: var(--nav-bar);
  /* z-index: 90; */
  padding: 0px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
}

.nav-subitem {
  font-size: larger;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  padding: 12px 26px 12px 26px;
  cursor: pointer;
  transition-property: background-color;
  transition-duration: 300ms;
}

.nav-subitem:hover {
  background-color: var(--nav-bar-hover);
}

@media (max-width: 1440px) {
  .audit-report-time {
    display: none;
  }
}

.page-message-padded {
  margin: 0px 0px 3.5rem 0px;
  padding: 0px 1.6rem;
  max-width: 1920px;
  width: 100%;
}

.content-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: auto;
  background-color: var(--base);
  /* z-index: 3; */
  position: relative;
  transition: background-color 250ms ease-in-out, color 350ms ease-in-out;
}

.content-cluster {
  margin: 0px 0px 3.5rem 0px;
  padding: 0px 1.6rem;
  max-width: 1920px;
  width: 100%;
  color: var(--text);
}

.content-cluster-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: var(--text);
}

.content-cluster-options {
  display: flex;
  justify-content: left;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  gap: 10px;
}

.content-cluster-children > p {
  font-size: 1.5rem;
  line-height: normal;
  color: var(--text);
}

.content-option-title,
.raid-server-title {
  color: var(--text);
  margin: 0px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.raid-server-title {
  font-size: 1.8rem;
}

.paginationPage {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7px;
  width: 40px;
  cursor: pointer;
  color: var(--primary-button-background);
  border: 1px solid var(--primary-button-background);
  opacity: 0.4;
  border-radius: 6px;
}

.paginationPage:hover,
.paginationPage.active {
  /* color: var(--text);
    border: 1px solid var(--text); */
  opacity: 1;
}

.audit-report-time {
  margin-left: auto;
}

.content-table-container {
  margin-bottom: 20px;
  border-bottom: 1px solid var(--gray5);
  width: 100%;
  height: 25rem;
}

.content-table-container.expanded {
  overflow-y: auto;
  height: auto;
}

.content-table {
  border-collapse: collapse;
  width: 100%;
}

.content-table-row-highlight {
  cursor: pointer;
}

.content-table-row-highlight:hover {
  background-color: var(--table-highlight) !important;
}

.content-table td,
.content-table th {
  border: 1px solid var(--gray5);
  text-align: left;
  padding: 7px;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.content-table th {
  background-color: var(--card) !important;
  font-weight: bolder !important;
  font-size: larger;
}

.content-table tr:nth-child(odd) {
  background-color: var(--table-odd-rows);
}

.nav-box {
  display: flex;
  text-decoration: none !important;
  justify-content: center;
  flex-direction: column;
  height: 150px;
  width: 440px;
  padding: 10px 30px 10px 30px;
  cursor: pointer;
  border-radius: 7px;
  transition: background-color 150ms ease-in-out, transform 150ms ease-in-out,
    filter 150ms ease-in-out;
  user-select: none;
}

.nav-box.glowing {
  position: relative;
  background-color: var(--base);
  z-index: 1;
}

@keyframes animateOpacity {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.5;
  }
}

.nav-box.glowing:before {
  content: "";
  z-index: -1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(-45deg, #60efff 40%, #ff00e6 60%);
  transform: translate3d(0px, 0px, 0) scale(1);
  filter: blur(10px);
  border-radius: inherit;

  animation-name: animateOpacity;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
}

.nav-box.glowing::after {
  content: "";
  z-index: -1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: inherit;
  border-radius: inherit;
}

.nav-box.small {
  height: min-content;
}

.nav-box.shrinkable {
  padding: 0.5rem 1.2rem;
}

.no-interact {
  cursor: default;
  pointer-events: none;
}

.main-nav-menu {
  position: fixed;
  z-index: 5;
  width: 100%;
}

.nav-box:hover:not(.no-interact) {
  /* transform: translate(-4px, -4px); */
  filter: drop-shadow(4px 4px 5px rgba(0, 0, 0, 0.198));
  background-color: var(--highlighted-1);
}

.community-box {
  display: flex;
  text-decoration: none !important;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  padding: 10px 30px 10px 30px;
  cursor: pointer;
  border-radius: 7px;
  transition: background-color 200ms;
  user-select: none;
}

.community-box-title {
  color: var(--text);
  margin: 0px;
}

.community-box-description {
  color: var(--text);
  font-size: 1.5rem;
  margin: 0px;
}

.community-box-link {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.5rem;
  color: var(--text-faded);
}

.nav-box-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 7px;
  margin-bottom: 5px;
}

.main-button {
  padding: 10px 20px;
  background-color: rgb(233, 233, 233);
  border-radius: 3px;
  cursor: pointer;
  transition: box-shadow 250ms;
  font-size: larger;
}

.main-button:hover {
  box-shadow: 0px 0px 10px 0px var(--white);
}

.community-box:hover {
  background-color: var(--highlighted-1);
}

.content-option-title,
.raid-server-title {
  color: var(--text);
  margin: 0px;
}

.content-option-description {
  color: var(--text-faded);
  font-size: larger;
  margin: 0px;
}

.server-status-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  color: var(--text);
  font-size: large;
}

.server-status-indicator {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 1.4rem;
}

.light-theme .nav-icon.should-invert,
.light-theme .nav-icon-large.should-invert,
.light-theme .link-icon.should-invert,
.light-theme .add-panel-icon.should-invert,
.light-theme .theme-icon.should-invert {
  filter: invert();
}

.primary-button {
  padding: 10px 20px;
  text-decoration: none !important;
  background-color: rgb(233, 233, 233);
  border-radius: 3px;
  cursor: pointer;
  transition: box-shadow 250ms;
  font-size: larger;
  background-color: white;
  color: black !important;
  user-select: none;
  width: max-content;
}

.secondary-button,
.toggle-button,
.donate-button-large,
.donate-button-small {
  padding: 10px 20px;
  text-decoration: none !important;
  border-radius: 3px;
  cursor: pointer;
  border: 1px solid white;
  background-color: var(--gray8);
  color: white !important;
  transition: box-shadow 250ms;
  font-size: larger;
  user-select: none;
  width: max-content;
}

.danger-button {
  padding: 10px 20px;
  text-decoration: none !important;
  border-radius: 3px;
  cursor: pointer;
  border: 1px solid red;
  background-color: var(--gray8);
  color: red !important;
  transition: box-shadow 250ms;
  font-size: larger;
  user-select: none;
  width: max-content;
}

.danger-button:hover {
  box-shadow: 0px 0px 10px 0px var(--red);
}

.secondary-button.expandable {
  transition: padding 200ms, box-shadow 250ms;
}

.primary-button.should-invert {
  background-color: var(--primary-button-background);
  color: var(--base) !important;
}

.secondary-button.should-invert,
.toggle-button {
  border: 2px solid var(--secondary-button-border);
  background-color: var(--base);
  color: var(--text) !important;
}

.primary-button:hover,
.secondary-button:hover {
  box-shadow: 0px 0px 10px 0px var(--primary-button-background);
}

.donate-button-large,
.donate-button-small {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 7px;
  transition: border 500ms, box-shadow 500ms;
}
.donate-button-large:hover {
  box-shadow: 0px 0px 10px 0px var(--donate);
  border: 1px solid var(--donate);
}

.donate-button-small {
  font-size: 1.1rem;
  opacity: 0.7;
  padding: 5px 10px;
  background-color: transparent !important;
  border: none !important;
}

.donate-button-small:hover {
  opacity: 1;
}

.primary-button.should-invert:hover,
.secondary-button.should-invert:hover,
.toggle-button:hover {
  box-shadow: 0px 0px 10px 0px var(--primary-button-background);
}

