    /* width: 165px; */
  }
  .nav-icon {
    margin: 0px;
  }
  .nav-item {
    overflow: hidden;
    flex: 1;
  }
  .nav-title {
    flex-direction: column;
  }
  .nav-push {
    display: block;
  }
  .nav-bar a {
    padding: 0px !important;
  }
  .nav-item a {
    width: 100%;
  }
  .footer {
    display: none;
  }
  .column-on-mobile {
    display: flex;
    justify-content: left !important;
    align-items: flex-start !important;
    flex-direction: column !important;
  }
  .hide-on-mobile {
    display: none !important;
  }
  .filter-bar {
    max-width: unset !important;
    position: fixed !important;
    top: 0px;
  }

  .banner-message {
    font-size: 1.2rem;
  }

  .vote-icon {
    width: 50px;
    height: 50px;
  }

  .poll {
    align-items: center;
  }
  .poll-option {
    align-items: center;
  }
  .poll-options {
    gap: 10px;
  }
  .poll-title {
    font-size: 1.8rem;
    text-align: center;
  }
  .notification-rule {
    padding: 10px 20px;
  }
}

@media (max-width: 500px) {
  .nav-box {
    width: 100%;
  }
  .nav-box.no-width {
    width: 100%;
  }
  .community-box {
    width: 100%;
  }
}

@media (max-width: 410px) {
  .social-member-entry {
    display: flex;
    flex-direction: column;
  }
  .social-member-entry.name {
    font-weight: bold;
  }
}

@media (max-width: 310px) {
  .hide-on-small-screen {
    display: none;
  }
}
